/** @jsx React.DOM */

var React = require('react');
var d3 = require('d3');
var Node = require('./Node.jsx');

var Tree = React.createClass({displayName: 'Tree',
  propTypes: {
    tree: React.PropTypes.array.isRequired,
    margins: React.PropTypes.object.isRequired,
    width: React.PropTypes.number.isRequired,
    height: React.PropTypes.number.isRequired,
  },

  render:function () {
    var root = this.props.tree[0];
    var diagonal = d3.svg.diagonal()
                         .projection(function(d)  {return [d.y, d.x];});
    var tree = d3.layout
                 .tree()
                 .size([this.props.height, this.props.width]);
    var gTransf = 'translate(' + this.props.margins.left + ',' +
                                 this.props.margins.top + ')';
    var nodes = tree.nodes(root);
    var links = tree.links(nodes);

    nodes.forEach(function(d)  {d.y = d.depth * 180;});

    var nodeElems = nodes.map(function(node, i) 
      {return Node({x: node.y, y: node.x, name: node.name, r: 10, key: i});});

    var linkElems = links.map(function(link, i) 
      {return React.DOM.path({key: i, className: 'Link', d: diagonal(link, i)});});

    return (
      React.DOM.g({transform: gTransf}, 
        linkElems, 
        nodeElems
      )
    );
  }
});

module.exports = Tree;



